<template>
  <div class="root">
  <div v-for="item in displayText">
    <DisplayQA :Question=item.Question :Answer=item.Answer />
  </div>
</div>
</template>

<script>
import DisplayQA  from "../../components/DisplayQA";
export default {
  name: "Deliver",
  data(){
    return {
      displayText:[
        {
          Question: 'Q: 是否只接收在线投递简历？',
          Answer:   'A: 投递职位的方式仅限线上投递，暂不接受线下简历。请务必在网申截止日期前，通过网申系统投递简历。'
        },
        {
          Question: 'Q: 是否可以申请多个职位？',
          Answer:   'A: 网申最多可投递两个职位，我们会依据简历匹配度进行笔面试安排。'
        }
      ]
    }
  },
  components:{
    DisplayQA
  }
}
</script>

<style scoped>

</style>



